import styled from "styled-components";
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

const SWhyTry = styled.section`
  background: radial-gradient(
      56.92% 42.11% at 111.57% 109.48%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      71.73% 87.75% at -17.08% -20.39%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;

  padding: 70px 0;
  border-radius: 30px;

  & > div {
    display: flex;
    align-items: center;
    flex-direction: column;

    & > p {
      text-align: center;
      margin-bottom: 42px;
    }
  }
`;

const Title = styled(Title2)`
  font-size: 26px;
`;

const WhyTryList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
  margin: 80px 0 50px 0;
`;

const WhyTryReason = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  background: #ffffff22;
  border-radius: 30px;
  gap: 25px;
  padding: 43px 5% 29px 5%;

  p {
    text-align: center;
    line-height: 150%;
    font-size: clamp(12px, 1.5vw, 20px);
  }

  img {
    width: 150px;
  }
`;

const WhyTry = ({ whytrylist, language }) => {
  return (
    <SWhyTry>
      <InnerWrap>
        <Title>
          Почему именно <br /> SocialStream?
        </Title>

        <WhyTryList>
          {whytrylist.map((whytry, index) => (
            <WhyTryReason key={index}>
              <img src={whytry.img} />
              <Text size="20">{whytry.text}</Text>
            </WhyTryReason>
          ))}
        </WhyTryList>
      </InnerWrap>
    </SWhyTry>
  );
};

export default WhyTry;
